/* main */
body {
  font-family: "Roboto", sans-serif;
  color: #000000;
  overflow-x: hidden;
  background-color: #ffffff;
}

.container {
  width: 1170px;
  padding: 0 10px;
  margin: 0 auto;
  max-width: 100%;
}

.image {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
}
.image img {
  object-fit: cover;
  height: 100%;
  width: 100%;
}

p {
  padding: 15px;
  background-color: #eeeeee;
  color: #000000;
  border-radius: 15px;
}

@media (min-width: 992px) {
  .step-back {
    display: none;
  }
}
@media (max-width: 992px) {
  .step-back {
    padding: 15px;
    background-color: #eeeeee;
    color: #000000;
    border-radius: 15px;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: opacity 0.3s ease 0s;
    margin-bottom: 20px;
  }
}

@media (max-width: 992px) {
  .text-desktop {
    display: none;
  }
}

@media (min-width: 992px) {
  .text-mobile {
    display: none;
  }
}

.preloader {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: white;
  z-index: 999;
  -webkit-animation: fade-in 0.6s;
  -moz-animation: fade-in 0.6s;
  animation: fade-in 0.6s;
  overflow: auto;
  display: flex;
  justify-content: center;
  align-items: center;
}
.preloader__row {
  width: 100px;
  height: 100px;
  position: relative;
  animation: preloader-rotate 1s infinite linear;
}
@keyframes preloader-rotate {
  100% {
    transform: rotate(360deg);
  }
}
.preloader__item {
  position: absolute;
  top: 50%;
  width: 40px;
  height: 40px;
  margin-top: -20px;
  border-radius: 50%;
  background-color: grey;
  animation: preloader-bounce 1s infinite ease-in-out;
}
.preloader__item:first-child {
  left: 0;
}
.preloader__item:last-child {
  right: 0;
}
@keyframes preloader-bounce {
  0%, 100% {
    transform: scale(1.5);
  }
  50% {
    transform: scale(0.5);
  }
}

.page1 {
  transition: all 0.5s ease-in 0s;
  opacity: 0;
  position: relative;
}
.page1__container {
  position: relative;
}
.page1__text {
  padding-top: 20px;
  padding-bottom: 20px;
  position: relative;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 7px;
}
.page1__text p {
  max-width: 500px;
}
@media (max-width: 992px) {
  .page1__text p {
    max-width: 100%;
  }
}
.page1__text .page1__text--first {
  opacity: 0;
  transition: all 0.3s linear 0s;
  transform: translate(0, 100%);
}
.page1__text .page1__text--first.active {
  opacity: 1;
  transform: translate(0, 0);
}

.page2 {
  position: relative;
  margin-top: -60vh;
  opacity: 0;
}
.page2__container {
  height: 200vh;
}
.page2__row {
  padding-top: 20px;
  display: flex;
  justify-content: space-between;
  flex-direction: column;
  position: relative;
  column-gap: 60px;
  height: 100vh;
}
.page2__text {
  flex: 1 1 100%;
  display: flex;
  justify-content: space-between;
}
.page2__left {
  flex: 0 1 auto;
}
.page2__left--first {
  max-width: 190px;
  height: 190px;
}
.page2__right {
  flex: 0 1 auto;
  max-width: 500px;
}
.page2__right--second {
  margin-top: 7px;
}
@media (max-width: 992px) {
  .page2__text {
    flex-direction: column;
    height: 100%;
  }
  .page2__left {
    order: 2;
    margin-bottom: 20px;
  }
  .page2__right {
    max-width: 100%;
    order: 1;
    flex: 1 1 100%;
  }
  .page2__left--first-mobile {
    max-width: 190px;
    height: 190px;
  }
  .page2__right--second-mobile {
    margin-top: 7px;
  }
}

.page3 {
  margin-top: -140vh;
  opacity: 0;
  position: relative;
}
.page3__container {
  position: relative;
  z-index: 1;
  height: 200vh;
}
.page3__row {
  padding-top: 20px;
  display: flex;
  justify-content: space-between;
  position: relative;
  column-gap: 60px;
  height: 100vh;
  width: 100%;
}
.page3__text {
  width: 100%;
  display: flex;
  justify-content: space-between;
}
.page3__left {
  flex: 0 1 auto;
}
.page3__left--first {
  max-width: 500px;
}
.page3__left--second {
  max-width: 500px;
  margin-top: 7px;
}
.page3__right {
  flex: 0 1 auto;
}
.page3__right--first {
  width: 190px;
  height: 190px;
}
@media (max-width: 992px) {
  .page3__row {
    display: flex;
    flex-direction: column;
  }
  .page3__text {
    height: 100%;
    display: flex;
    flex-direction: column;
  }
  .page3__left {
    height: 100%;
    flex: 1 1 100%;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
  }
  .page3__left--first-mobile {
    margin-bottom: auto;
  }
  .page3__left--second-mobile {
    width: 190px;
    height: 190px;
    margin-bottom: 7px;
  }
  .page3__right--first-mobile {
    margin-bottom: 20px;
  }
}

